import React from 'react'
import { Link } from 'react-router-dom'

const Collection = () => (
  <section className="collectionsec">
    <div className="container">
      <div className="row">
        <div className="col-sm-12 col-md-12 heading-line">
          <h4 className="section-title">
            <img src="assets/images/box.svg" alt='' className="head-icon" />
            Collection
          </h4>
        </div>
      </div>
      <div className="row">
        {/* Team Block One */}
        <div className="col-sm-12 col-md-4 collection-blockbg">
          <div className="collection-block">
            <div className="mkt-img">
              <img
                src="assets/images/collection.png"
                alt="collection"
                className="img-fluid"
              />
            </div>
            <div className="mkt-article">
              <h4>Vlad Genesis Collection</h4>
              <p>
                by <Link to="/">Beeple</Link>
              </p>
              <p>
                Lorem ipsum dolor sit amet, consectetur adipiscing elit. Sed
                mauris mi felis scelerisque tincidunt egestas sed.
              </p>
            </div>
            <ul>
              <li>
                <Link to="/vlad-collection" className="btn-link">
                  Explore Collection
                </Link>
              </li>
            </ul>
          </div>
        </div>
        {/* Team Block One */}
        <div className="col-sm-12 col-md-4 collection-blockbg">
          <div className="collection-block">
            <div className="mkt-img">
              <img
                src="assets/images/collection.png"
                alt="collection"
                className="img-fluid"
              />
            </div>
            <div className="mkt-article">
              <h4>Shibari Collection</h4>
              <p>
                by <Link to="/vlad-collection">Sushinobi</Link>
              </p>
              <p>
                Lorem ipsum dolor sit amet, consectetur adipiscing elit. Sed
                mauris mi felis scelerisque tincidunt egestas sed.
              </p>
            </div>
            <ul>
              <li>
                <Link to="/" className="btn-link">
                  Explore Collection
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </section>
)


export default Collection